@import "../styles/_colors.scss";

.popUpBody {

    font-size: 15px !important;

    background-color: $gray !important;

    border: none !important;

    color: #FFFFFF !important;

    text-align: end !important;

}



.modal-content {

    border-radius: 0.8rem !important;

}



.popUpHeader {

    background-color: $gray !important;

    display: flex !important;

    flex-wrap: nowrap !important;

    align-content: center !important;

    align-items: center !important;

    border: none !important;

    border-top-left-radius: calc(0.8rem - 1px) !important;

    border-top-right-radius: calc(0.8rem - 1px) !important;



    .close {

        position: absolute !important;

        left: 0 !important;

        top: 16px !important;

        border: none !important;

        background-color: inherit !important;

        color: #FFFFFF !important;

    }

}



.popUpFooter {

    background-color: $gray !important;

    border: none !important;

    justify-content: flex-start !important;

    border-bottom-left-radius: calc(0.8rem - 1px) !important;

    border-bottom-right-radius: calc(0.8rem - 1px) !important;

}



.popUpButton {

    width: 104px !important;

    height: 28px !important;

    display: flex !important;

    font-size: 13px !important;

    color: #FFFFFF !important;

    background-color: $purple !important;

    align-content: center !important;

    justify-content: center !important;

    align-items: center !important;

    white-space: nowrap;

    border-radius: 0.8rem !important;



}



.popUpButtonDeny {

    width: 104px !important;

    height: 28px !important;

}



.popUpButton:hover {

    background-color: $purple !important;

    color: #FFFFFF !important;

}



.popUpButtonDeny:hover {

    background-color: #F83A3A !important;

    color: #FFFFFF !important;

}